import './Minics.scss';
import VideoGrid from '../VideoGrid/VideoGrid';
import Deeplink from '../Deeplink/Deeplink';
import { useRef } from 'react';

function Minics({ videosData, socialsData }) {
  const appframeRef = useRef(null);
  return (
    <section className="minics">
      <h2 className="minics__title"></h2>
      <ul className="minics__list">
        <li className="minics__list-item">  </li>
        <li className="minics__list-item">
                
        </li>
        <li className="minics__list-item">   </li>
        <li className="minics__list-item">
          , ,  
        </li>
      </ul>
      <VideoGrid videosData={videosData} />
      <div className="minics__buy">
        <p className="minics__sale-price">2500 &#8381;</p>
        <p className="minics__full-price">3500 &#8381;</p>
        <Deeplink
          id="whatsapp"
          styleType="icon"
          socialsData={socialsData}
          appframeRef={appframeRef}
        />
        <Deeplink
          id="telegram"
          styleType="icon"
          socialsData={socialsData}
          appframeRef={appframeRef}
        />
        <Deeplink
          id="instagram"
          styleType="icon"
          socialsData={socialsData}
          appframeRef={appframeRef}
        />
        <Deeplink
          id="facebook"
          styleType="icon"
          socialsData={socialsData}
          appframeRef={appframeRef}
        />
        <iframe
          id="app-frame"
          src=""
          title="app-frame"
          style={{ display: 'none' }}
          // className="deeplink__app-frame"
          ref={appframeRef}
        ></iframe>
      </div>
    </section>
  );
}

export default Minics;
